<template>
  <div class="like-movie">
    <div class="top"></div>
    <div class="my-collection">
      <van-nav-bar title="我的收藏" left-text="返回" left-arrow @click-left="back" />
    </div>
    <div class="movie-detail-info">
       <div class="clearfix movie-detail-box" v-for="(item, index) in currentLikeMovieData" :key="index" :id="item.id" @click="viewMovieDetail({name: 'movieDetail', params: {id: item.id}})">
         <div class="fl theme-pictures">
           <div class="">
            <div><img class="auto-img" :src="item.images.small" alt=""></div>
          </div> 
         </div>
          <div class="fl thematic-content">
            <div class="clearfix movie-delete">
              <span class="fl movie-title one-text">{{item.title}}</span>
              <span class="fr collection" @click.stop="deleteLikeMovie(item.id)">
                <van-icon name="delete" />
              </span>
            </div>
            <div class="movie-plot">{{item.genresInfo}}</div>
            <div class="movie-time clearfix">
              <div class="fl">
                <van-icon name="comment-circle-o" />
                <span>{{item.comments}}</span>
              </div>
              <div class="fl">
                <van-icon name="underway-o" />
                <span>{{item.durations[0]}}</span>
              </div>
            </div>
            <div class="score clearfix">
              <div class="fl clearfix star-box-count">
              <!-- 灰星 -->
              <div ref="noactivestar" class="noactive-star fl">
                <span>
                  <van-icon name="star-o" />
                </span>
                <span>
                  <van-icon name="star-o" />
                </span>
                <span>
                  <van-icon name="star-o" />
                </span>
                <span>
                  <van-icon name="star-o" />
                </span>
                <span>
                  <van-icon name="star-o" />
                </span>
              </div>
              <!-- 黄星 -->
              <div class="active-star" :style="{width: item.width + 'px'}">
                  <span>
                    <van-icon name="star-o" />
                  </span>
                  <span>
                    <van-icon name="star-o" />
                  </span>
                  <span>
                    <van-icon name="star-o" />
                  </span>
                  <span>
                    <van-icon name="star-o" />
                  </span>
                  <span>
                    <van-icon name="star-o" />
                  </span>
                </div>
              </div>
              <!-- 分数 -->
              <div class="fraction fl">{{item.rating.average}}</div>
            </div>
          </div>
        </div> 
    </div>
  </div>
</template>

<script>

import {createNamespacedHelpers} from 'vuex'

const {mapState} = createNamespacedHelpers('likeMovieModule');

export default {
  name: "likeMovie",

  computed: {
    ...mapState(['currentLikeMovieData'])
  },

  methods: {
    //返回上一级
    back() {
      this.$router.go(-1);
    },

    //查看电影详情
    viewMovieDetail(o) {
      this.$router.push(o);
    },

    //删除我的收藏电影
    deleteLikeMovie(id) {
      // console.log('id :', id);
      for (let i = 0; i < this.currentLikeMovieData.length; i++) {
        if (id == this.currentLikeMovieData[i].id) {
          this.$store.dispatch('likeMovieModule/deleteLikeMovie', i)
          .then(() => {
            // console.log('修改本地存储数据')
            let userStatus = JSON.parse(localStorage.getItem("user"));

            //获取所有用户电影数据
            let likeMovieData = JSON.parse(localStorage.getItem('likeMovieData'));

            likeMovieData[userStatus.username] = this.currentLikeMovieData;
            localStorage.setItem('likeMovieData', JSON.stringify(likeMovieData));
          })
          break;
        }
      }
    }
  },

  created() {
    //验证登录
    let userStatus = localStorage.getItem("user");
    // console.log('userStatus :', userStatus);

    if (userStatus) {
      let userInfo = JSON.parse(userStatus);

      //如果登录
      if (userInfo.isLogin) {
        //开启加载提示
        this.$toast.loading({
          duration: 0,
          message: '加载中...'
        });

        //获取所有用户收藏的电影数据
        let likeMovieData = localStorage.getItem('likeMovieData');

        likeMovieData = likeMovieData ? JSON.parse(likeMovieData) : {};

        //获取当前用户收藏的电影数据
        let currentLikeMovieData = likeMovieData[userInfo.username];

        currentLikeMovieData = currentLikeMovieData ? currentLikeMovieData : [];

        //生成页面数据
        this.$store.commit('likeMovieModule/getCurrentLikeMovieData', currentLikeMovieData);

        this.$toast.clear();
      } else {
        //如果未登录
        this.$router.push({name: 'login'});
      }
    } else {
      //未登录
      this.$router.push({name: 'login'});
    }
  }
} 
</script>

<style lang="less" scoped>
  .like-movie {
    // font-size: 16px;
    .top {
      height: 24px;
      background-color: #000;
    }

    .my-collection {
      margin-bottom: 20;
    }

    .movie-detail-info {
      margin: 0px 10px;
      overflow-y: auto;
      position: fixed;
      top: 90px;
      left: 0;
      bottom: 0;
      right: 0;

    }

    .movie-detail-box {
      background-color: #fff;
      padding: 10px 0;
      margin-bottom: 10px;
    }

    .theme-pictures {
      width: 100px;
      margin: 0 10px;
      border-radius: 8px;
      overflow: hidden;
    }

    .thematic-content {
      width: calc(~"100% - 120px");
    }

    .movie-delete {
      margin-top: 10px;
    }

    .movie-title {
      font-size: 18px;
      line-height: 40px;
      color: #ff9e01;
      width: 80%;
    }

    .collection {
      font-size: 20px;
      margin-right: 10px;
      margin-top: 6px;
    }

    .movie-plot, .movie-time {
      font-size: 14px;
      color: #666;
      line-height: 26px;
      span {
        margin: 0 12px 0 4px;
      }
    }

    .score {
      position: relative;
      font-size: 16px;
      text-align: center;
      margin-top: 10px;

      .active-star {
        position: absolute;
        top: 0;
        left: 0;
        color: #FDC03D;
        height: 16px;
        width: 60%;
        overflow: hidden;
      }
      
      .noactive-star {
        width: 80px;
        height: 16px;
      }
      .star-box-count {
        width: 80px;
        position: relative;
        span {
          position: absolute;
          width: 16px;
          height: 16px;
          top: 0;
        &:nth-child(1){
          left: 0;
        }
        &:nth-child(2){
          left: 16px;
        }
        &:nth-child(3){
          left: 32px;
        }
        &:nth-child(4){
          left: 48px;
        }
        &:nth-child(5){
          left: 64px;
        }
        }
      }
      .fraction {
        font-size: 16px;
        margin-left: 20px;
        color: #FDC03D;
        width: 20px;
      }
    }
  }
</style>